.header-style {
    height: 170px;
    padding: 0;
    position: relative;
}

.background-style {
    background: url(../../public/ghibli-header.jpg) no-repeat center / cover;
    background-position: 0 61%;
    width: 100%;
    height: 100%;
}

.title-paragraph-style {
    font-size: 60px;
    color: #000;
    text-shadow: #fff 2px -2px 3px;
    position: absolute;
    left: 4%;
    bottom: 20%;
    font-family: PermanentMarkerRegular;
}

.content-style {
    min-height: 700px;
    background: linear-gradient(90deg, rgba(153, 216, 184, 1) 0%, rgba(77, 138, 19, 1) 100%);
    padding-top: 5px;
}

.footer-style {
    background: linear-gradient(90deg, rgba(153, 216, 184, 1) 0%, rgba(77, 138, 19, 1) 100%);
    padding: 20px 4%;
}

.footer-paragraph {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
    font-size: 15px;
}

.content-divider {
    border-top: 2px solid black;
    margin-top: 20px;
    margin-bottom: 5px;
}

.flexible-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.ant-tabs-nav {
    background: linear-gradient(90deg, rgba(153, 216, 184, 1) 0%, rgba(77, 138, 19, 1) 100%);
    border-radius: 0 0 10px 10px;
    margin-bottom: 0;
}

.ant-tabs-top>.ant-tabs-nav::before {
    border: 0;
}

.ant-tabs>.ant-tabs-nav {
    margin-bottom: 0;
}

.ant-tabs-content-holder {
    background-color: #ffffe0;
    padding-top: 15px;
    border-radius: 10px;
}

#rc-tabs-1-panel-1 {
    min-height: 200px;
    padding-bottom: 32px;
}

#rc-tabs-1-panel-2 {
    min-height: 200px;
    padding-top: 32px;
    padding-bottom: 32px;
}

.select-a-favorite {
    display: block;
    margin-top: 10px;
    text-align: center;
}

.select-a-favorite-text {
    font-size: 30px;
    font-family: PermanentMarkerRegular;
}

.select-a-favorite-icon {
    font-size: 50px;
}